/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {

 background-color: var(--global-bg-color);
 td, th {
 color: var(--global-text-color);
 background-color: var(--global-bg-color);
 max-width: 100%;
 }
 td {
 font-size: 1rem;
 background-color: var(--global-bg-color);
 max-width:100%;
 }

 th {
 font-size: 1rem;
 background-color: var(--global-bg-color);
 max-width:100%;
 }
}

a, table.table a {
 color: var(--global-theme-color);


 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }


}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0.5em 10px;
 font-size: 1.2rem;
}

hr {
border-color: $grey-color-light;
background-color: $grey-color-light;
color: $grey-color-light;
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.9rem;
 margin-top: 0.9rem;
 margin-bottom: 1.5rem;
 text-align: left;
 color: var(--global-text-color);
 transition: all 0.2s;
}

// Card

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 100%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;


 .row {
 display: flex;

 }

 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 margin-right:-0.1rem;
 width: 100%;
 max-width:100%;


 .address {
 margin-bottom: 0px;
 margin-top: 0px;
 font-family: monospace;

 p {
 display: inline-block;

 }


 }
}



/* THIS IS FOR DESKTOP, ABOVE IS FOR MOBILES.*/
@media screen and (min-width: 500px) {
 .profile {
 margin:3rem;
 width: 25%;
 left:0px;

 .address {
 p { display: inline; }
 }
 }



}



.post-description {
 margin-bottom: 2rem;
 font-size: 0.875rem;
 position:relative;
 display:block;
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}


// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;

}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}
.dropdown-item {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}
.navbar.navbar-light {
 a {
 &:hover {
 text-decoration: none;
 }
 }
 .navbar-brand {
 color: var(--global-text-color);
 }
 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

// Social (bottom)

.social {
 text-align: center;
 .contact-icons {
 font-size: 2rem;
 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
 .contact-note {
 font-size: 0.8rem;
 }
}


// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
